<template>
  <div class="block">
    <Row :gutter="20">
      <i-col
        :xs="12"
        :md="8"
        :lg="6"
        v-for="(infor, i) in inforCardData"
        :key="`infor-${i}`"
        style="height: 120px;padding-bottom: 10px;"
      >
        <Card shadow borderd>
          <div class="flex">
            <span class="flex-title">{{ infor.title }}</span>
            <count-to :end="infor.count" class="flex-num" />
          </div>
        </Card>
      </i-col>
    </Row>
  </div>
</template>
<script>
import "iview/dist/styles/iview.css";
import countTo from "./CountComponent";
export default {
  components: {
    countTo
  },
  data() {
    return {
      inforCardData: [
        {
          title: "交易总额",
          icon: "md-person-add",
          count: 1023215523,
          color: "#2d8cf0"
        },
        {
          title: "订单总量",
          icon: "md-locate",
          count: 3200913,
          color: "#19be6b"
        },
        {
          title: "今日订单",
          icon: "md-share",
          count: 657,
          color: "#ed3f14"
        },
        {
          title: "今日访问",
          icon: "md-help-circle",
          count: 142101,
          color: "#ff9900"
        }
      ]
    };
  },
  mounted() {
    setTimeout(() => {
      this.randomNum();
    }, 3000);
  },
  methods: {
    randomNum() {
      setInterval(() => {
        let dayUv = parseInt(Math.random() * 100);
        this.inforCardData[3].count = this.inforCardData[3].count + dayUv;
      }, 2500);

      setInterval(() => {
        let dayNum = 10 - parseInt(Math.random() * 5);
        this.inforCardData[2].count = this.inforCardData[2].count + dayNum;
      }, 3000);

      setInterval(() => {
        let totalOrder = 10 - parseInt(Math.random() * 5);
        this.inforCardData[1].count = this.inforCardData[1].count + totalOrder;
      }, 3200);

      setInterval(() => {
        let totalNum = parseInt(Math.random() * 4000);
        this.inforCardData[0].count = this.inforCardData[0].count + totalNum;
      }, 3500);
    }
  }
};
</script>
